  transition: transform .3s cubic-bezier(.4, 0, .2, 1);
}

.search-form__btn:hover .search-form__icon {
  transform: scale(1.15);
}

.switch {
  height: 20px;
  width: 40px;
  display: inline-block;
  position: relative;
}

.switch__input {
  display: none;
}

.switch__slider {
  cursor: pointer;
  border: .604701px solid #fd5103;
  border-radius: 20px;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1), border-color .3s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.switch__slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #fd5103;
  border-radius: 50%;
  transition: transform .3s cubic-bezier(.4, 0, .2, 1), background-color .3s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 2px;
  left: 2px;
}

.switch__input--header:checked + .switch__slider:before, .switch__input:checked + .switch__slider:before {
  background-color: #fff;
  transform: translateX(20px);
}

.switch__input:checked + .switch__slider {
  background-color: #fd5103;
  border-color: #fff;
}

.select {
  width: 100%;
  z-index: 100;
  position: relative;
}

.select.open .select__dropdown, .select.open .select__backdrop {
  display: block;
}

.select__input {
  height: 34px;
  cursor: pointer;
  color: #fd5103;
  border: 1px solid #fd5103;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  font-size: 16px;
  line-height: 1.35;
  display: flex;
}

.select__dropdown {
  max-height: 84px;
  border: 1px solid #fd5103;
  border-radius: 4px;
  display: none;
  position: absolute;
  top: 34px;
  left: 0;
  right: 0;
  overflow-y: auto;
}

.select__backdrop {
  z-index: -1;
  background: none;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.select__item {
  color: #5f6775;
  padding: 6.5px 10px;
  font-size: 10px;
  line-height: 1.5;
  transition: background-color .3s cubic-bezier(.4, 0, .2, 1), color .3s cubic-bezier(.4, 0, .2, 1);
}

.select__item.selected {
  color: #fff;
  background-color: #fd5103;
}

.select__item:hover {
  color: #fff;
  cursor: pointer;
  background-color: #fd5103;
}

