.gallery-title {
    font-weight: 600;
    font-size: 22px;
    line-height: 191%;
    text-align: center;
    color: inherit;
    margin-bottom: 60px;
  
    @media screen and (min-width: 768px) {
      font-weight: 700;
      font-size: 32px;
      line-height: 133%;
      
      text-align: center;
    }
  
    @media screen and (min-width: 1280px) {
      font-size: 48px;
      line-height: 150%;
    }
  
  }

.cocktails {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
  
  
    border-radius: 4px;
    
}

.cocktail-info {
    font-weight: 900;
    font-size: 26px;
    line-height: 162%;
    text-align: center;
    color: inherit;
    margin-top: 20px;
    margin-bottom: 27px;
}

.cocktails__card {
    text-align: center;
    margin: auto;
    padding-bottom: 20px;
    max-width: 395px;
    border: 1px solid #fd5103;
    border-radius: 4px;

    @media screen and (min-width: 768px) {
        width: calc((100% - 1 * 30px) / 2);
    }

    @media screen and (min-width: 1280px) {
        width: calc((100% - 2 * 30px) / 3);
    }

    &:hover,
    &:focus {
        box-shadow: 0px 1px 1px rgba(230, 111, 8, 0.12),
            0px 4px 4px rgba(220, 125, 17, 0.06),
            1px 4px 6px rgba(220, 124, 6, 0.16);
        transform: scale(.98);
    }

}

.btn-lm {
    display: inline-flex;
    text-align: center;
  
    padding: 8px 16px;
    margin-right: 24px;
  
    background-color: #fe7031;
    color: #ffffff;
    border: none;
    border-radius: 4px;
  

    font-weight: 400;
    font-size: 14px;
    line-height: 171%;
    text-align: center;
    letter-spacing: 0.02em;
  
    cursor: pointer;
  

      
    &:hover{
      background-color: #ffffff;
      color: #fd5103;
      box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
      0px 4px 4px rgba(0, 0, 0, 0.06),
      1px 4px 6px rgba(0, 0, 0, 0.16);
    }
  
    &:active,
    &:focus{
      background-color: #fd5103;
      color: #FCFCFC;
    }
  
  }
  
  .heard{
    display: none;
  
    &:active{
      display: block;
    }
  }
  
  .btn-add {
    position: relative;
    display: inline-flex;
    text-align: center;
    width: 110px;
    height: 39px;
  
    padding: 8px 16px;
  
    background-color: transparent;
  
    color: #fe7031;
    border: 1px solid #fe7031;
    border-radius: 4px;
  
 
    font-weight: 400;
    font-size: 14px;
    line-height: 171%;
    text-align: center;
    letter-spacing: 0.02em;
  
    cursor: pointer;
  
  
  
    &::before {
      position: absolute;
      content: '';
      top: 95%;
      left: 75%;
      transform: translate(-50%, -150%);
      width: 21px;
      height: 19px;
      background-image: url(../images/Heart.png);
      background-repeat: no-repeat;
    }
  
    &:hover::after{
      position: absolute;
      
      content: '';
      top: 95%;
      left: 75%;
      transform: translate(-50%, -150%);
      width: 21px;
      height: 19px;
      background-image: url(../images/Heart2.png);
      background-repeat: no-repeat;
    }
  }


  .cocktails__img {
    width:100%;
    display: block;
  }

  .btn-loadm {
 
    text-align: center;

    padding: 8px 16px;
    margin-top: 20px;
    margin-left: calc((50%) - 51px);
    margin-right: auto;

    background-color: #fe7031;
    color: #ffffff;
    border: none;
    border-radius: 4px;

  
    font-weight: 400;
    font-size: 14px;
    line-height: 171%;
    text-align: center;
    letter-spacing: 0.02em;

    cursor: pointer;


    &:hover {
        background-color: #ffffff;
        color: #fd5103;
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
            0px 4px 4px rgba(0, 0, 0, 0.06),
            1px 4px 6px rgba(0, 0, 0, 0.16);
    }

    &:active,
    &:focus {
        background-color: #fd5103;
        color: #FCFCFC;
    }

  }